import { render, h } from 'preact';
import { useEffect, useState } from 'preact/hooks';
import { nanoid } from 'nanoid'
import htm from 'htm';
import picostyle from "picostyle";

const { style, css } = picostyle(h, { returnObject: true });

const enableTransitions = false;

const flexCenter = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}

const Container = style("div")({
  ...flexCenter,
  background: '#ccc',
  width: '100%',
  height: '100vh',
  flexDirection: 'row',
  flexWrap: 'wrap',
  overflow: 'hidden'
});

const expandedStyles = scale => ({
  width: '100%',
  height: '100vh',
  position: 'absolute',
  zIndex: '1000',
  transform: `scale(${scale})`
})

const nonExpandedStyles = {
  borderRadius: '20px',
  transform: 'scale(1)',
  ":hover": {
    zIndex: '1000',
    cursor: 'pointer',
    transform: 'scale(1.5)'
  },
};

const PatternContainer = style("div")(props => ({
  ...flexCenter,
  backgroundColor: props.backgroundColor,
  margin: '10px',
  height: `${props.size}px`,
  width: `${props.size}px`,
  transition: enableTransitions ? 'transform 0.1s' : '',
  ...(props.isExpanded ? expandedStyles(props.expandedScale) : nonExpandedStyles)
}));

const Close = style("span")(props => ({
  position: 'absolute',
  top: '0',
  right: '0',
  zIndex: '1000',
  transform: 'rotate(45deg)',
  padding: '50px',
  fontSize: '4em',
  lineHeight: '0',
  cursor: 'pointer',
  color: `${props.color}`
}));

const Circle = style("div")(props => ({
  background: 'rgba(0,0,0,0)',
  width: `${props.size}px`,
  height: `${props.size}px`,
  border: `${props.color} solid ${props.borderSize}px`,
  borderRadius: '50%',
  overflow: 'hidden',
  position: 'absolute'
}));

const thinCircle = (size, color, r1, r2, r3, r4, r5, r6) => {
  const offsetVariance = 10;
  const sizeVariance = 100;
  const sizeWithVariance = size + r1 * sizeVariance;
  const xOffsetKey = r2 < 0.5 ? 'left' : 'right';
  const yOffsetKey = r3 < 0.5 ? 'top' : 'bottom';

  const maxBorderSize = 2;
  const isSmall = size < 80;
  const staticBorderWidth = isSmall ? 0.5 : 1;
  let borderWidthWithVariance = 0;
  if (!isSmall) {
    borderWidthWithVariance = Math.max(r6 * maxBorderSize, staticBorderWidth);
    console.log(borderWidthWithVariance);
  }
  const borderWidth = isSmall ? staticBorderWidth : borderWidthWithVariance;

  return css({
    background: 'rgba(0,0,0,0)',
    width: `${sizeWithVariance}px`,
    height: `${sizeWithVariance}px`,
    border: `${color} solid ${borderWidth}px`,
    position: 'absolute',
    borderRadius: '50%',
    transition: enableTransitions ? 'all 1s ease' : '',
    [xOffsetKey]: `${r4 * offsetVariance}px`,
    [yOffsetKey]: `${r5 * offsetVariance}px`,
  })
};

const lineBox = (size, r) => css({
  background: 'rgba(0,0,0,0)',
  width: `${size}px`,
  transform: `rotate(${r * 360}deg)`,
  position: 'absolute',
  height: `${size}px`,
  transition: enableTransitions ? 'all 1s ease' : '',
});

const lineTop = (size, color, r1, r2) => {
  const staticLineWidth = 1;
  const maxLineSize = 2;
  const isSmall = size < 50;
  let lineWidthWithVariance = 0;
  if (!isSmall) {
    lineWidthWithVariance = Math.floor(r2 * maxLineSize) + 0.5;
  }
  const lineWidth = isSmall ? staticLineWidth : lineWidthWithVariance;

  return css({
    height: `${size + r1}px`,
    borderBottom: `${color} solid ${lineWidth}px`,
  });
};

const lineBottom = (size, r) => css({
  height: `${size + r}px`,
});

function genRArrays(nArrays) {
  return new Array(nArrays).fill(null).map(() => genRs(7));
}

function genRs(nRs) {
  return new Array(nRs).fill(null).map(() => Math.random());
}

function genRInt(maxN, minN = 0) {
  return Math.max(Math.floor(Math.random() * maxN), minN);
}

function genRBool() {
  return Math.random() < 0.5;
}

function Pattern() {
  const id = nanoid();
  const maxNumCircles = 12;
  const minDiameter = 30;
  const maxDiameter = 200;
  const maxNumLines = 4;
  const padding = maxDiameter / 4;

  const [inverse, setInverse] = useState(genRBool());
  const [diameter, setDiameter] = useState(genRInt(maxDiameter, minDiameter));
  const [numCircles, setNumCircles] = useState(genRInt(maxNumCircles));
  const [numLines, setNumLines] = useState(genRInt(maxNumLines));
  const [rArrs, setRArrs] = useState(genRArrays(numCircles + numLines));

  const [isExpanded, setIsExpanded] = useState(false);
  const expandedScale = (maxDiameter/diameter) * 2;

  const color = inverse ? '#aaa' : '#555';
  const bgColor = inverse ? '#555' : '#aaa';

  function update() {
    const newNumCircles = genRInt(maxNumCircles);
    const newNumLines = genRInt(maxNumLines);
    const newRArrs = genRArrays(newNumCircles + newNumLines);

    setInverse(prevInverse => !prevInverse);
    setNumCircles(newNumCircles);
    setNumLines(newNumLines);
    setDiameter(genRInt(maxDiameter, minDiameter));
    setRArrs(newRArrs);
  }

  function onKeyDown(event) {
    event.preventDefault();

    if (event.code === 'Space') {
      update();
    } else if (event.code === 'Escape') {
      setIsExpanded(false);
    }
  }

  useEffect(() => {
    window.addEventListener('keydown', onKeyDown);

    return () => {
      window.removeEventListener('keydown', onKeyDown);
    };
  }, []);

  return (
    <>
      <PatternContainer size={diameter + padding} color={color} backgroundColor={bgColor} id={id} onClick={() => setIsExpanded(true)} isExpanded={isExpanded} expandedScale={expandedScale}>
        <Circle size={diameter} borderSize={Math.ceil(diameter/(maxDiameter/4))} color={color}>
          {Array(numCircles).fill('').map((_el, i) => (
            <div className={thinCircle(diameter, color, rArrs[i][0], rArrs[i][1], rArrs[i][2], rArrs[i][3], rArrs[i][4], rArrs[i][5])} />
          ))}
          {Array(numLines).fill('').map((_el, i) => (
            <div className={lineBox(diameter, rArrs[numCircles + i][5])}>
              <div className={lineTop(diameter/2, color, rArrs[numCircles + i][6] * diameter, rArrs[numCircles + i][i])} />
              <div className={lineBottom(diameter/2, rArrs[numCircles + i][6] * diameter)} />
            </div>
          ))}
        </Circle>
      </PatternContainer>
      {isExpanded && <Close color={color} onClick={() => setIsExpanded(false)}>+</Close>}
    </>
  );
}

function App() {
  const numPatterns = 24;

  return (
      <Container>
        {Array(numPatterns).fill('').map(Pattern)}
        </Container>
    );
}

render(htm.bind(h)`<${App} />`, document.body);
